import Vue from 'vue'
import VueRouter from 'vue-router'

import AuthView from '@/views/auth/AuthView'
import FinancasView from '@/views/financas/FinancasView'
import LembretesView from '@/views/lembretes/LembretesView'
import Error404View from '../views/Error404View'


Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    name: 'error404',
    component: Error404View
  },

  {
    path: '/',
    name: 'auth',
    component: AuthView
  },

  {
    path: '/financas/',
    name: 'financas',
    component: FinancasView
  },

  {
    path: '/lembretes/',
    name: 'lembretes',
    component: LembretesView
  },  
]

const router = new VueRouter({
  routes
})

export default router
